<template><div class="markdown-body"><h1>对话框</h1>
<p><code v-pre="">mu-dialog</code> 用于提示用户作一些决定，或者是完成某个任务时需要的一些其它额外的信息。</p>
<h2 id="shi-li"><a class="header-anchor" href="#shi-li" aria-hidden="true">¶</a> 示例</h2>
<p>点击对话框之外，或者按下后退，点击关闭按钮即可关闭对话框。</p>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container&gt;\n  &lt;mu-flex justify-content=\&quot;center\&quot;&gt;\n    &lt;mu-button @click=\&quot;openSimpleDialog\&quot;&gt;Open Dialog&lt;/mu-button&gt;\n  &lt;/mu-flex&gt;\n  &lt;mu-dialog title=\&quot;Dialog\&quot; width=\&quot;360\&quot; :open.sync=\&quot;openSimple\&quot;&gt;\n    this is simple Dialog\n    &lt;mu-button slot=\&quot;actions\&quot; flat color=\&quot;primary\&quot; @click=\&quot;closeSimpleDialog\&quot;&gt;Close&lt;/mu-button&gt;\n  &lt;/mu-dialog&gt;\n&lt;mu-container&gt;\n\n&lt;/mu-container&gt;&lt;/mu-container&gt;&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      openSimple: false\n    };\n  },\n  methods: {\n    openSimpleDialog () {\n      this.openSimple = true;\n    },\n    closeSimpleDialog () {\n      this.openSimple = false;\n    }\n  }\n};\n&quot;,&quot;style&quot;:null}">
                    <div class="source" slot="source"><mu-container>
  <mu-flex justify-content="center">
    <mu-button @click="openSimpleDialog">Open Dialog</mu-button>
  </mu-flex>
  <mu-dialog title="Dialog" width="360" :open.sync="openSimple">
    this is simple Dialog
    <mu-button slot="actions" flat="" color="primary" @click="closeSimpleDialog">Close</mu-button>
  </mu-dialog>
<mu-container>

</mu-container></mu-container></div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-container</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-flex</span> <span class="hljs-attr">justify-content</span>=<span class="hljs-string">"center"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"openSimpleDialog"</span>&gt;</span>Open Dialog<span class="hljs-tag">&lt;/<span class="hljs-name">mu-button</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-flex</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-dialog</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"Dialog"</span> <span class="hljs-attr">width</span>=<span class="hljs-string">"360"</span> <span class="hljs-attr">:open.sync</span>=<span class="hljs-string">"openSimple"</span>&gt;</span>
    this is simple Dialog
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-button</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"actions"</span> <span class="hljs-attr">flat</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"closeSimpleDialog"</span>&gt;</span>Close<span class="hljs-tag">&lt;/<span class="hljs-name">mu-button</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-dialog</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">mu-container</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">openSimple</span>: <span class="hljs-literal">false</span>
    };
  },
  <span class="hljs-attr">methods</span>: {
    openSimpleDialog () {
      <span class="hljs-keyword">this</span>.openSimple = <span class="hljs-literal">true</span>;
    },
    closeSimpleDialog () {
      <span class="hljs-keyword">this</span>.openSimple = <span class="hljs-literal">false</span>;
    }
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="alerts"><a class="header-anchor" href="#alerts" aria-hidden="true">¶</a> Alerts</h2>
<p>设置 <code v-pre="">overlay-close</code> 和 <code v-pre="">esc-press-close</code> 使点击对话框外部或按下后退不会关闭。</p>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container&gt;\n  &lt;mu-flex justify-content=\&quot;center\&quot;&gt;\n    &lt;mu-button @click=\&quot;openAlertDialog\&quot;&gt;Open Dialog&lt;/mu-button&gt;\n  &lt;/mu-flex&gt;\n  &lt;mu-dialog title=\&quot;Use Google's location service?\&quot; width=\&quot;600\&quot; max-width=\&quot;80%\&quot; :esc-press-close=\&quot;false\&quot; :overlay-close=\&quot;false\&quot; :open.sync=\&quot;openAlert\&quot;&gt;\n    Let Google help apps determine location. This means sending anonymous location data to Google, even when no apps are running.\n    &lt;mu-button slot=\&quot;actions\&quot; flat color=\&quot;primary\&quot; @click=\&quot;closeAlertDialog\&quot;&gt;Disagree&lt;/mu-button&gt;\n    &lt;mu-button slot=\&quot;actions\&quot; flat color=\&quot;primary\&quot; @click=\&quot;closeAlertDialog\&quot;&gt;Agree&lt;/mu-button&gt;\n  &lt;/mu-dialog&gt;\n&lt;mu-container&gt;\n\n&lt;/mu-container&gt;&lt;/mu-container&gt;&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      openAlert: false\n    };\n  },\n  methods: {\n    openAlertDialog () {\n      this.openAlert = true;\n    },\n    closeAlertDialog () {\n      this.openAlert = false;\n    }\n  }\n};\n&quot;,&quot;style&quot;:null}">
                    <div class="source" slot="source"><mu-container>
  <mu-flex justify-content="center">
    <mu-button @click="openAlertDialog">Open Dialog</mu-button>
  </mu-flex>
  <mu-dialog title="Use Google's location service?" width="600" max-width="80%" :esc-press-close="false" :overlay-close="false" :open.sync="openAlert">
    Let Google help apps determine location. This means sending anonymous location data to Google, even when no apps are running.
    <mu-button slot="actions" flat="" color="primary" @click="closeAlertDialog">Disagree</mu-button>
    <mu-button slot="actions" flat="" color="primary" @click="closeAlertDialog">Agree</mu-button>
  </mu-dialog>
<mu-container>

</mu-container></mu-container></div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-container</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-flex</span> <span class="hljs-attr">justify-content</span>=<span class="hljs-string">"center"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"openAlertDialog"</span>&gt;</span>Open Dialog<span class="hljs-tag">&lt;/<span class="hljs-name">mu-button</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-flex</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-dialog</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"Use Google's location service?"</span> <span class="hljs-attr">width</span>=<span class="hljs-string">"600"</span> <span class="hljs-attr">max-width</span>=<span class="hljs-string">"80%"</span> <span class="hljs-attr">:esc-press-close</span>=<span class="hljs-string">"false"</span> <span class="hljs-attr">:overlay-close</span>=<span class="hljs-string">"false"</span> <span class="hljs-attr">:open.sync</span>=<span class="hljs-string">"openAlert"</span>&gt;</span>
    Let Google help apps determine location. This means sending anonymous location data to Google, even when no apps are running.
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-button</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"actions"</span> <span class="hljs-attr">flat</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"closeAlertDialog"</span>&gt;</span>Disagree<span class="hljs-tag">&lt;/<span class="hljs-name">mu-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-button</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"actions"</span> <span class="hljs-attr">flat</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"closeAlertDialog"</span>&gt;</span>Agree<span class="hljs-tag">&lt;/<span class="hljs-name">mu-button</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-dialog</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">mu-container</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">openAlert</span>: <span class="hljs-literal">false</span>
    };
  },
  <span class="hljs-attr">methods</span>: {
    openAlertDialog () {
      <span class="hljs-keyword">this</span>.openAlert = <span class="hljs-literal">true</span>;
    },
    closeAlertDialog () {
      <span class="hljs-keyword">this</span>.openAlert = <span class="hljs-literal">false</span>;
    }
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="ke-gun-dong-de-dui-hua-kuang"><a class="header-anchor" href="#ke-gun-dong-de-dui-hua-kuang" aria-hidden="true">¶</a> 可滚动的对话框</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container&gt;\n  &lt;mu-flex justify-content=\&quot;center\&quot;&gt;\n    &lt;mu-button @click=\&quot;openScrollDialog\&quot;&gt;Open Scroll Dialog&lt;/mu-button&gt;\n  &lt;/mu-flex&gt;\n  &lt;mu-dialog title=\&quot;Phone Ringtone\&quot; width=\&quot;360\&quot; scrollable :open.sync=\&quot;openScroll\&quot;&gt;\n    &lt;mu-list&gt;\n      &lt;mu-list-item :key=\&quot;option\&quot; v-for=\&quot;option in options\&quot;&gt;\n        &lt;mu-list-item-content&gt;\n          &lt;mu-radio :label=\&quot;option\&quot; :value=\&quot;option\&quot; v-model=\&quot;ringtone\&quot;&gt;&lt;/mu-radio&gt;\n        &lt;/mu-list-item-content&gt;\n      &lt;/mu-list-item&gt;\n    &lt;/mu-list&gt;\n    &lt;mu-button slot=\&quot;actions\&quot; flat color=\&quot;primary\&quot; @click=\&quot;closeScrollDialog\&quot;&gt;ok&lt;/mu-button&gt;\n  &lt;/mu-dialog&gt;\n&lt;mu-container&gt;\n\n&lt;/mu-container&gt;&lt;/mu-container&gt;&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      openScroll: false,\n      ringtone: 'None',\n      options: [\n        'None',\n        'Atria',\n        'Callisto',\n        'Dione',\n        'Ganymede',\n        'Hangouts Call',\n        'Luna',\n        'Oberon',\n        'Phobos',\n        'Pyxis',\n        'Sedna',\n        'Titania',\n        'Triton',\n        'Umbriel'\n    ]\n    };\n  },\n  methods: {\n    openScrollDialog () {\n      this.openScroll = true;\n    },\n    closeScrollDialog () {\n      this.openScroll = false;\n    }\n  }\n};\n&quot;,&quot;style&quot;:null}">
                    <div class="source" slot="source"><mu-container>
  <mu-flex justify-content="center">
    <mu-button @click="openScrollDialog">Open Scroll Dialog</mu-button>
  </mu-flex>
  <mu-dialog title="Phone Ringtone" width="360" scrollable="" :open.sync="openScroll">
    <mu-list>
      <mu-list-item :key="option" v-for="option in options">
        <mu-list-item-content>
          <mu-radio :label="option" :value="option" v-model="ringtone"></mu-radio>
        </mu-list-item-content>
      </mu-list-item>
    </mu-list>
    <mu-button slot="actions" flat="" color="primary" @click="closeScrollDialog">ok</mu-button>
  </mu-dialog>
<mu-container>

</mu-container></mu-container></div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-container</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-flex</span> <span class="hljs-attr">justify-content</span>=<span class="hljs-string">"center"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"openScrollDialog"</span>&gt;</span>Open Scroll Dialog<span class="hljs-tag">&lt;/<span class="hljs-name">mu-button</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-flex</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-dialog</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"Phone Ringtone"</span> <span class="hljs-attr">width</span>=<span class="hljs-string">"360"</span> <span class="hljs-attr">scrollable</span> <span class="hljs-attr">:open.sync</span>=<span class="hljs-string">"openScroll"</span>&gt;</span>
