        <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item-content</span>&gt;</span>
          <span class="hljs-tag">&lt;<span class="hljs-name">mu-radio</span>  <span class="hljs-attr">:label</span>=<span class="hljs-string">"option"</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"option"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"ringtone"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-radio</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item-content</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-button</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"actions"</span> <span class="hljs-attr">flat</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"closeScrollDialog"</span>&gt;</span>ok<span class="hljs-tag">&lt;/<span class="hljs-name">mu-button</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-dialog</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">mu-container</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">openScroll</span>: <span class="hljs-literal">false</span>,
      <span class="hljs-attr">ringtone</span>: <span class="hljs-string">'None'</span>,
      <span class="hljs-attr">options</span>: [
        <span class="hljs-string">'None'</span>,
