        <span class="hljs-string">'Sedna'</span>,
        <span class="hljs-string">'Titania'</span>,
        <span class="hljs-string">'Triton'</span>,
        <span class="hljs-string">'Umbriel'</span>
    ]
    };
  },
  <span class="hljs-attr">methods</span>: {
    openScrollDialog () {
      <span class="hljs-keyword">this</span>.openScroll = <span class="hljs-literal">true</span>;
