<h2 id="quan-ping-dui-hua-kuang"><a class="header-anchor" href="#quan-ping-dui-hua-kuang" aria-hidden="true">¶</a> 全屏对话框</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container&gt;\n  &lt;mu-flex justify-content=\&quot;center\&quot;&gt;\n    &lt;mu-button @click=\&quot;openFullscreenDialog\&quot;&gt;Open Scroll Dialog&lt;/mu-button&gt;\n  &lt;/mu-flex&gt;\n  &lt;mu-dialog width=\&quot;360\&quot; transition=\&quot;slide-bottom\&quot; fullscreen :open.sync=\&quot;openFullscreen\&quot;&gt;\n    &lt;mu-appbar color=\&quot;primary\&quot; title=\&quot;Fullscreen Diaolog\&quot;&gt;\n      &lt;mu-button slot=\&quot;left\&quot; icon @click=\&quot;closeFullscreenDialog\&quot;&gt;\n        &lt;mu-icon value=\&quot;close\&quot;&gt;&lt;/mu-icon&gt;\n      &lt;/mu-button&gt;\n      &lt;mu-button slot=\&quot;right\&quot; flat @click=\&quot;closeFullscreenDialog\&quot;&gt;\n        Done\n      &lt;/mu-button&gt;\n    &lt;/mu-appbar&gt;\n    &lt;div style=\&quot;padding: 24px;\&quot;&gt;\n      this is a fullscreen dialog\n    &lt;/div&gt;\n  &lt;/mu-dialog&gt;\n&lt;mu-container&gt;\n\n&lt;/mu-container&gt;&lt;/mu-container&gt;&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      openFullscreen: false\n    };\n  },\n  methods: {\n    openFullscreenDialog () {\n      this.openFullscreen = true;\n    },\n    closeFullscreenDialog () {\n      this.openFullscreen = false;\n    }\n  }\n};\n&quot;,&quot;style&quot;:null}">
                    <div class="source" slot="source"><mu-container>
  <mu-flex justify-content="center">
    <mu-button @click="openFullscreenDialog">Open Scroll Dialog</mu-button>
  </mu-flex>
  <mu-dialog width="360" transition="slide-bottom" fullscreen="" :open.sync="openFullscreen">
    <mu-appbar color="primary" title="Fullscreen Diaolog">
      <mu-button slot="left" icon="" @click="closeFullscreenDialog">
        <mu-icon value="close"></mu-icon>
      </mu-button>
      <mu-button slot="right" flat="" @click="closeFullscreenDialog">
        Done
      </mu-button>
    </mu-appbar>
    <div style="padding: 24px;">
      this is a fullscreen dialog
    </div>
  </mu-dialog>
<mu-container>

</mu-container></mu-container></div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-container</span>&gt;</span>
