    <span class="hljs-tag">&lt;<span class="hljs-name">mu-appbar</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"Fullscreen Diaolog"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-button</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"left"</span> <span class="hljs-attr">icon</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"closeFullscreenDialog"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">mu-icon</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"close"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-icon</span>&gt;</span>
