<h2 id="dialog-props"><a class="header-anchor" href="#dialog-props" aria-hidden="true">¶</a> Dialog Props</h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>介绍</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>open</td>
<td>是否打开</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>title</td>
<td>dialog 标题，可用 <code v-pre="">slot=&quot;title&quot;</code> 代替</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>width</td>
<td>dialog 宽度，默认是 <code v-pre="">auto</code></td>
<td>String,Number</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>max-width</td>
<td>dialog 最大宽度</td>
<td>String,Number</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>scrollable</td>
<td>是否可以内部滚动</td>
<td>Boolean</td>
<td>false</td>
<td>false</td>
</tr>
<tr>
<td>fullscreen</td>
<td>是否是全屏</td>
<td>Boolean</td>
<td>false</td>
<td>false</td>
</tr>
<tr>
<td>transition</td>
<td>过渡动画方案</td>
<td>String</td>
<td>slide-top/slide-bottom/slide-left/slide-right/fade/scale</td>
<td>scale</td>
</tr>
<tr>
<td>dialog-class</td>
<td>dialog 样式</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>overlay</td>
<td>是否有遮盖层</td>
<td>Boolean</td>
<td>—</td>
<td>true</td>
</tr>
<tr>
<td>overlay-close</td>
<td>点击遮盖层是否可以关闭</td>
<td>Boolean</td>
<td>—</td>
<td>true</td>
</tr>
<tr>
<td>overlay-opacity</td>
<td>遮盖层的透明度</td>
<td>Boolean</td>
<td>—</td>
<td>0.4</td>
</tr>
<tr>
<td>overlay-color</td>
<td>遮盖层的颜色</td>
<td>String</td>
<td>—</td>
<td>#000</td>
</tr>
<tr>
<td>esc-press-close</td>
<td>按下 <code v-pre="">esc</code> 键是否可以关闭</td>
<td>Boolean</td>
<td>—</td>
<td>true</td>
</tr>
<tr>
<td>append-body</td>
<td>弹出层是否添加到 body 元素后, 内部使用</td>
<td>Boolean</td>
<td>—</td>
<td>true</td>
</tr>
</tbody>
</table>
<h2 id="dialog-events"><a class="header-anchor" href="#dialog-events" aria-hidden="true">¶</a> Dialog Events</h2>
<table>
<thead>
<tr>
<th>名称</th>
<th>介绍</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>close</td>
<td>当点击遮盖层或者按 esc 键，需要关闭弹层时触发</td>
<td>(reason) reason 关闭的原因  <br> <code v-pre="">overlay</code> : 点击了遮盖层 <br> <code v-pre="">esc</code> 按下 esc 键</td>
</tr>
</tbody>
</table>

</div class="markdown-body"></template>

<script>
export default {
  data () {
    return {
      openSimple: false,
      openAlert: false,
      openScroll: false,
      openFullscreen: false,
      ringtone: 'None',
      options: [
        'None',
        'Atria',
        'Callisto',
        'Dione',
        'Ganymede',
        'Hangouts Call',
        'Luna',
        'Oberon',
        'Phobos',
        'Pyxis',
        'Sedna',
        'Titania',
        'Triton',
        'Umbriel'
    ]
    };
  },
  methods: {
    openSimpleDialog () {
      this.openSimple = true;
    },
    closeSimpleDialog () {
      this.openSimple = false;
    },
    openAlertDialog () {
      this.openAlert = true;
    },
    closeAlertDialog () {
      this.openAlert = false;
    },
    openScrollDialog () {
      this.openScroll = true;
    },
    closeScrollDialog () {
      this.openScroll = false;
    },
    openFullscreenDialog () {
      this.openFullscreen = true;
    },
    closeFullscreenDialog () {
      this.openFullscreen = false;
    }
  }
};
</script>


